<template>
  <div>
    <Content :style="{padding: '24px', minHeight: '500px', background: '#fff'}">
      <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
        <Row>
          <Col span="8">
            <FormItem label="" prop="name">
              <Input type="text" v-model="formValidate.name" placeholder="" :maxlength="20" clearable></Input>
            </FormItem>
          </Col>
          <Col span="8">
            <FormItem label="" prop="number">
              <Input v-model="formValidate.number" placeholder="18" :maxlength="18" show-word-limit clearable></Input>
            </FormItem>
          </Col>
          <Col span="8">
            <FormItem label="" prop="phone">
              <Input v-model="formValidate.phone" placeholder="11" :maxlength="11" show-word-limit clearable></Input>
            </FormItem>
          </Col>
        </Row>
        <Row>
          <Col span="8">
            <FormItem label="" prop="marriage">
              <Select v-model="formValidate.marriage" placeholder="">
                <Option value="1"></Option>
                <Option value="2"></Option>
                <Option value="3"></Option>
                <Option value="4"></Option>
              </Select>
            </FormItem>
          </Col>
          <Col span="8">
            <FormItem label="" prop="spouse">
              <Input type="text" v-model="formValidate.spouse" placeholder="" :maxlength="20" clearable></Input>
            </FormItem>
          </Col>
          <Col span="8">
            <FormItem label="" prop="phone2">
              <Input v-model="formValidate.phone2" placeholder="11" :maxlength="11" show-word-limit clearable></Input>
            </FormItem>
          </Col>
        </Row>
        <Row>
          <Col span="8">
            <FormItem label="" prop="department">
              <Select v-model="formValidate.department_id" placeholder="">
                <Option v-for="item in departmentList" :value="item.value" :key="item.value">{{ item.label }}</Option>
              </Select>
            </FormItem>
          </Col>
          <Col span="8">
            <FormItem label="" prop="bank">
              <Select v-model="formValidate.bank_id" placeholder="">
                <Option v-for="item in bankList" :value="item.value" :key="item.value">{{ item.label }}</Option>
              </Select>
            </FormItem>
          </Col>
          <Col span="8">
            <FormItem label="" prop="type">
              <Select v-model="formValidate.type_id" placeholder="">
                <Option v-for="item in typeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
              </Select>
            </FormItem>
          </Col>
        </Row>
        <Row>
          <Col span="8">
            <FormItem label="" prop="business">
              <Input v-model="formValidate.business" placeholder="" clearable></Input>
            </FormItem>
          </Col>
          <Col span="8">
            <FormItem label="" prop="address">
              <Input v-model="formValidate.address" placeholder="" clearable></Input>
            </FormItem>
          </Col>
          <Col span="8">
            <FormItem label="" prop="state">
              <Select v-model="formValidate.state" placeholder="">
                <Option value="1"></Option>
                <Option value="2"></Option>
                <Option value="3"></Option>
                <Option value="4"></Option>
                <Option value="5"></Option>
              </Select>
            </FormItem>
          </Col>
        </Row>
        <Row>
          <Col span="20">
            <FormItem label="" prop="project">
              <Input v-model="formValidate.project" placeholder="" clearable></Input>
            </FormItem>
          </Col>
          <Col span="4">
            <FormItem label="" prop="base">
              <Input v-model="formValidate.time" placeholder="8" maxlength="8" type="number" clearable number/>
            </FormItem>
          </Col>
        </Row>
        <Row>
          <Col span="8">
            <FormItem label="" prop="money">
              <Input v-model="formValidate.money" placeholder="" type="number"  number maxlength="2">
                <span slot="append"></span>
              </Input>
            </FormItem>
          </Col>
          <Col span="8">
            <FormItem label="" prop="month">
              <Input v-model="formValidate.month" placeholder="" type="number"  number maxlength="2">
                <span slot="append"></span>
              </Input>
            </FormItem>
          </Col>
          <Col span="8">
            <FormItem label="" prop="base">
              <Input v-model="formValidate.base" placeholder="" type="number"  number maxlength="3">
                <span slot="append">%</span>
              </Input>
            </FormItem>
          </Col>
        </Row>
        <Row>
          <Col span="24">
            <FormItem label="" prop="remark">
              <Input v-model="formValidate.remark" type="textarea" :autosize="{minRows: 3,maxRows: 4}" placeholder=""></Input>
            </FormItem>
          </Col>
        </Row>
        <Row>
          <Col span="8">
            &nbsp;
          </Col>
          <Col span="8">
            <FormItem>
              <Button type="primary" @click="goSave('formValidate')" :disabled="dis"></Button>
              <Button @click="goReset('formValidate')" style="margin-left: 8px" :disabled="dis"></Button>
              <Button type="dashed" style="margin-left: 8px" @click="goBack" :disabled="dis"></Button>
            </FormItem>
          </Col>
          <Col span="8">
            &nbsp;
          </Col>
        </Row>
      </Form>
    </Content>
  </div>
</template>
<script>
import * as API from './API.js'
import axios from 'axios'
import IdentityCodeValid from '../../plugins/checkId'
export default {
  data () {
    const validateName = (rule, value, callback) => {
      if (!value) {
        callback(new Error(''))
      } else if (/^[\u4E00-\u9FA5]{2,8}$/.test(value)) {
        callback()
      } else {
        callback(new Error('2-8'))
      }
    }
    const validateNumber = (rule, value, callback) => {
      if (!value) {
        callback(new Error('18'))
      } else if (/^\d{18}$|^\d{17}(\d|X)$/.test(value)) {
        var tip = IdentityCodeValid(value)
        if (tip === '' || value === '000000000000000000') {
          callback()
        } else {
          callback(new Error(tip))
        }
      } else {
        callback(new Error('18X'))
      }
    }
    const validatePhone = (rule, value, callback) => {
      if (!value) {
        callback(new Error('11'))
      } else if (/^[1][0-9]{10}$/.test(value) || value === '00000000000') {
        callback()
      } else {
        callback(new Error('111'))
      }
    }
    return {
      dis: false,
      formValidate: {
        name: '',
        number: '',
        phone: '',
        marriage: '1',
        spouse: '',
        phone2: '',
        department_id: '1',
        bank_id: '1',
        type_id: '1',
        state: '1',
        money: 20,
        month: 24,
        base: 100,
        time: '',
        business: '',
        address: '',
        project: '',
        remark: ''
      },
      ruleValidate: {
        name: [
          { required: true, validator: validateName, trigger: 'blur' }
        ],
        number: [
          { required: true, validator: validateNumber, trigger: 'blur' }
        ],
        phone: [
          { required: true, validator: validatePhone, trigger: 'blur' }
        ],
        business: [
          { required: true, message: '' }
        ],
        address: [
          { required: true, message: '' }
        ],
        project: [
          { required: true, message: '' }
        ],
        marriage: [
          { required: true }
        ],
        // department: [
        //   { required: true }
        // ],
        // bank: [
        //   { required: true }
        // ],
        // type: [
        //   { required: true }
        // ],
        time: [
          { required: true, message: ''  }
        ],
        money: [
          { required: true, message: ''  }
        ],
        month: [
          { required: true, message: ''  }
        ],
        base: [
          { required: true, message: ''  }
        ]
      }
    }
  },
  created: function () {
    this.departmentList = this.evil('(' + localStorage.getItem("departmentList") + ')')
    this.bankList = this.evil('(' + localStorage.getItem("bankList") + ')')
    this.typeList = this.evil('(' + localStorage.getItem("typeList") + ')')
    this.formValidate.time = this.getYearMonthDate()
  },
  methods: {
    // Function
    evil (fn) {
      let Fn = Function
      return new Fn('return ' + fn)()
    },
    goSave (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.dis = true
          this.$Loading.start()
          axios.get(API.add, {
            params: {
              form: this.formValidate
            }
          }).then(res => {
            if (res.data === 'OK') {
              this.$Loading.finish()
              this.$Message.success('!')
              this.$Notice.success({
                title: '!',
                desc: '' + this.formValidate.name + ''
              })
              setTimeout(() => {
                this.$router.push({ path: '/Loan' })
                this.dis = false
                this.$refs[name].resetFields()
              }, 1000)
            } else {
              this.dis = false
              this.$Loading.error()
              this.$Notice.error({
                title: res.data
              })
            }
          }).catch(res => {
            this.dis = false
            this.$Loading.error()
            this.$Notice.error({
              title: '!'
            })
          })
        } else {
          this.$Message.error('!')
        }
      })
    },
    goReset (name) {
      this.$refs[name].resetFields()
    },
    goBack () {
      this.$router.push({ path: '/Loan' })
    },
    getYearMonthDate(){
      var myDate = new Date();
      var y = myDate.getFullYear() + '';
      var m = myDate.getMonth() + 1
      var d = myDate.getDate();
      if (m< 10){
        var n = '0' + m + '';
      }else{
        var n = m + ''
      }
      if (d<10){
        var t = '0' + d + '';
      }else{
        var t = d + ''
      }
      return y + n + t + ''
    }
  }
}
</script>

