<template>
  <div>
    <Row :style="{margin: '5px 1px 0px 1px'}">
      <Col span="4">
        <Select v-model="queryStr.department_id" placeholder="" prefix="md-easel" clearable="true">
          <Option v-for="item in departmentList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </Col>
      <Col span="4">
        <Select v-model="queryStr.bank_id" placeholder="" prefix="md-cube" clearable="true">
          <Option v-for="item in bankList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </Col>
      <Col span="4">
        <Select v-model="queryStr.type_id" placeholder="" prefix="md-swap" clearable="true">
          <Option v-for="item in typeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </Col>
      <Col span="4">
        <Select v-model="queryStr.state" placeholder="" prefix="ios-bookmark" clearable="true">
          <Option value="1"></Option>
          <Option value="2"></Option>
          <Option value="3"></Option>
          <Option value="4"></Option>
          <Option value="5"></Option>
        </Select>
      </Col>
      <Col span="4">
        <Select v-model="queryStr.money" placeholder="" prefix="logo-yen" clearable="true">
          <Option value="1">1</Option>
          <Option value="2">2</Option>
          <Option value="3">3</Option>
          <Option value="4">4</Option>
          <Option value="5">5</Option>
          <Option value="6">6</Option>
          <Option value="7">7</Option>
          <Option value="8">8</Option>
          <Option value="9">9</Option>
          <Option value="10">10</Option>
          <Option value="11">11</Option>
          <Option value="12">12</Option>
          <Option value="13">13</Option>
          <Option value="14">14</Option>
          <Option value="15">15</Option>
          <Option value="16">16</Option>
          <Option value="17">17</Option>
          <Option value="18">18</Option>
          <Option value="19">19</Option>
          <Option value="20">20</Option>
        </Select>
      </Col>
      <Col span="4">
        <Select v-model="queryStr.month" placeholder="" prefix="md-calendar" clearable="true">
          <Option value="1">1</Option>
          <Option value="2">2</Option>
          <Option value="3">3</Option>
          <Option value="4">4</Option>
          <Option value="5">5</Option>
          <Option value="6">6</Option>
          <Option value="7">7</Option>
          <Option value="8">8</Option>
          <Option value="9">9</Option>
          <Option value="10">10</Option>
          <Option value="11">11</Option>
          <Option value="12">12</Option>
          <Option value="13">13</Option>
          <Option value="14">14</Option>
          <Option value="15">15</Option>
          <Option value="16">16</Option>
          <Option value="17">17</Option>
          <Option value="18">18</Option>
          <Option value="19">19</Option>
          <Option value="20">20</Option>
          <Option value="21">21</Option>
          <Option value="22">22</Option>
          <Option value="23">23</Option>
          <Option value="24">24</Option>
        </Select>
      </Col>
    </Row>
    <Row :style="{margin: '0px 1px 5px 1px'}">
      <Col span="4">
        <Input type="text" v-model="queryStr.name" placeholder="" prefix="md-person" clearable="true"></Input>
      </Col>
      <Col span="4">
        <Input type="text" v-model="queryStr.number" placeholder="" prefix="md-browsers" clearable="true"></Input>
      </Col>
      <Col span="4">
        <Input type="text" v-model="queryStr.phone" placeholder="" prefix="md-phone-portrait" clearable="true"></Input>
      </Col>
      <Col span="8">
        <Input type="text" v-model="queryStr.business" placeholder="" prefix="md-albums" clearable="true"></Input>
      </Col>
      <Col span="4">
        <Button-group>
          <Button type="info" @click="goQuery">
            <Icon type="md-search"></Icon>
            
          </Button>
          <Button @click="goRefresh">
            <Icon type="md-refresh"></Icon>
            
          </Button>
        </Button-group>
      </Col>
    </Row>
    <Table
      highlight-row
      @on-row-dblclick="onRowDblclick"
      :loading="loading"
      :context="self"
      :border="border"
      :stripe="stripe"
      :size="size"
      :columns="columns"
      :data="pageList">
      <template slot-scope="{ row }" slot="name">
        {{row.name}}
      </template>
      <template slot-scope="{ row }" slot="number">
        {{row.number}}
      </template>
      <template slot-scope="{ row }" slot="department">
        {{row.department}}
      </template>
      <template slot-scope="{ row }" slot="state">
        {{row.state}}
      </template>
      <template slot-scope="{ row }" slot="bank">
        {{row.bank}}
      </template>
      <template slot-scope="{ row }" slot="type">
        {{row.type}}
      </template>
      <template slot-scope="{ row }" slot="time">
        {{row.time}}
      </template>
      <template slot-scope="{ row }" slot="money">
        {{row.money}}
      </template>
      <template slot-scope="{ row }" slot="month">
        {{row.month}}
      </template>
      <template slot-scope="{ row }" slot="base">
        {{row.base}}
      </template>
      <template slot-scope="{ row }" slot="business">
        {{row.business}}
      </template>
      <template slot-scope="{ row }" slot="address">
        {{row.address}}
      </template>
      <template slot-scope="{ row }" slot="project">
        {{row.project}}
      </template>
      <template slot-scope="{ row }" slot="sex">
        {{row.sex}}
      </template>
      <template slot-scope="{ row }" slot="birth">
        {{row.birth}}
      </template>
      <template slot-scope="{ row }" slot="marriage">
        {{row.marriage}}
      </template>
      <template slot-scope="{ row }" slot="phone">
        {{row.phone}}
      </template>
      <template slot-scope="{ row }" slot="spouse">
        {{row.spouse}}
      </template>
      <template slot-scope="{ row }" slot="phone2">
        {{row.phone2}}
      </template>
      <template slot-scope="{ row, index }" slot="action" >
        <Button type="primary" size="small" style="margin-right: 5px" @click="goEdit(index)"></Button>
      </template>
    </Table>
    <div class="right">
      <Page
        :total="pageTotal"
        :current="pageCurrent"
        :page-size="pageSize"
        @on-page-size-change="onPageSizeChange"
        @on-change="onChange"
        show-sizer
        show-elevator
        show-total>
      </Page>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import * as API from './API.js'

export default {
  name: 'list',
  data () {
    return {
      queryURL: API.query,
      totalURL: API.total,
      pageTotal: 0,
      pageCurrent: 1,
      pageSize: 10,
      pageList: [],
      loading: true,
      border: true,
      stripe: true,
      size: 'small',
      height: 450,
      self: this,
      departmentList: [],
      bankList: [],
      typeList: [],
      queryStr: {
        department_id: '',
        bank_id: '',
        type_id: '',
        state: '',
        money: '',
        month: '',
        name: '',
        number: '',
        phone: '',
        business: ''
      },
      columns: [
        {
          title: '',
          key: 'name',
          slot: 'name',
          width: 150,
          tooltip: true,
          resizable: true,
          fixed: 'left'
        },
        {
          title: '',
          key: 'number',
          width: 180,
          tooltip: true,
          resizable: true,
          fixed: 'left'
        },
        {
          title: '',
          key: 'department',
          width: 180,
          tooltip: true,
          resizable: true,
          fixed: 'left'
        },
        {
          title: '',
          key: 'state',
          tooltip: true,
          resizable: true,
          width: 180
        },
        {
          title: '',
          key: 'bank',
          tooltip: true,
          resizable: true,
          width: 180
        },
        {
          title: '',
          key: 'type',
          tooltip: true,
          resizable: true,
          width: 180
        },
        {
          title: '',
          key: 'time',
          tooltip: true,
          resizable: true,
          width: 180
        },
        {
          title: '',
          key: 'money',
          tooltip: true,
          resizable: true,
          width: 180
        },
        {
          title: '',
          key: 'month',
          tooltip: true,
          resizable: true,
          width: 180
        },
        {
          title: '',
          key: 'base',
          tooltip: true,
          resizable: true,
          width: 180
        },
        {
          title: '',
          key: 'business',
          tooltip: true,
          resizable: true,
          width: 180
        },
        {
          title: '',
          key: 'address',
          tooltip: true,
          resizable: true,
          width: 180
        },
        {
          title: '',
          key: 'project',
          tooltip: true,
          resizable: true,
          width: 180
        },
        {
          title: '',
          key: 'sex',
          tooltip: true,
          resizable: true,
          width: 180
        },
        {
          title: '',
          key: 'birth',
          tooltip: true,
          resizable: true,
          width: 180
        },
        {
          title: '',
          key: 'marriage',
          tooltip: true,
          resizable: true,
          width: 180
        },
        {
          title: '',
          key: 'phone',
          tooltip: true,
          resizable: true,
          width: 180
        },
        {
          title: '',
          key: 'spouse',
          tooltip: true,
          resizable: true,
          width: 180
        },
        {
          title: '',
          key: 'phone2',
          tooltip: true,
          resizable: true,
          width: 180
        },
        {
          title: '',
          slot: 'action',
          align: 'center',
          width: 180,
          tooltip: true,
          resizable: true,
          fixed: 'right',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.goEdit(params.index)
                  }
                }
              }, '')
            ]);
          }
        }
      ]
    }
  },
  created: function () {
    this.departmentList = this.evil('(' + localStorage.getItem("departmentList") + ')')
    this.bankList = this.evil('(' + localStorage.getItem("bankList") + ')')
    this.typeList = this.evil('(' + localStorage.getItem("typeList") + ')')
    this.getLists(this.$store.state.queryStr, this.$store.state.pageCurrent, this.$store.state.pageSize)
  },
  methods: {
    // Function
    evil (fn) {
      let Fn = Function
      return new Fn('return ' + fn)()
    },
    getLists (queryStr, pageCurrent, pageSize) {
      this.loading = true
      axios.get(this.queryURL, {
        params: {
          queryStr: queryStr,
          pageCurrent: pageCurrent,
          pageSize: pageSize
        }
      }).then(res => {
        axios.get(this.totalURL, {
          params: {
            queryStr: queryStr
          }
        }).then(response => {
          this.pageList = res.data
          this.pageTotal = response.data
          this.loading = false
        }).catch(response => {
          this.$Loading.error()
          this.$Notice.error({
            title: ',!'
          })
          this.loading = false
        })
      }).catch(res => {
        this.$Loading.error()
        this.$Notice.error({
          title: '!'
        })
        this.loading = false
      })
    },
    // 
    onPageSizeChange (value) {
      this.pageSize = value
      this.pageCurrent = 1
      this.$store.commit('setPageSize', {
        pageSize: value
      })
      this.$store.commit('setPageCurrent', {
        pageCurrent: 1
      })
      this.getLists(this.$store.state.queryStr, this.$store.state.pageCurrent, this.$store.state.pageSize)
    },
    // 
    onChange (value) {
      this.pageCurrent = value
      this.$store.commit('setPageCurrent', {
        pageCurrent: value
      })
      this.getLists(this.$store.state.queryStr, this.$store.state.pageCurrent, this.$store.state.pageSize)
    },
    goQuery () {
      this.pageCurrent = 1
      this.$store.commit('setQueryStr', {
        queryStr: this.queryStr
      })
      this.$store.commit('setPageCurrent', {
        pageCurrent: 1
      })
      this.getLists(this.$store.state.queryStr, this.$store.state.pageCurrent, this.$store.state.pageSize)
    },
    goRefresh () {
      this.queryStr = {
        department_id: '',
        bank_id: '',
        type_id: '',
        state: '',
        money: '',
        month: '',
        name: '',
        number: '',
        phone: '',
        business: ''
      },
      this.pageCurrent = 1
      this.$store.commit('setQueryStr', {
        queryStr: {
          department_id: '',
          bank_id: '',
          type_id: '',
          state: '',
          money: '',
          month: '',
          name: '',
          number: '',
          phone: '',
          business: ''
        }
      })
      this.$store.commit('setPageCurrent', {
        pageCurrent: 1
      })
      this.getLists(this.$store.state.queryStr, this.$store.state.pageCurrent, this.$store.state.pageSize)
    },
    onRowDblclick (row, index) {
      this.$Modal.info({
        title: `---ID:${this.pageList[index].id}`,
        content: `${this.pageList[index].name}<br>
                  ${this.pageList[index].number}<br>
                  ${this.pageList[index].department}<br>
                  ${this.pageList[index].state}<br>
                  ${this.pageList[index].bank}<br>
                  ${this.pageList[index].type}<br>
                  ${this.pageList[index].time}<br>
                  ${this.pageList[index].money}<br>
                  ${this.pageList[index].month}<br>
                  ${this.pageList[index].base}<br>
                  ${this.pageList[index].business}<br>
                  ${this.pageList[index].address}<br>
                  ${this.pageList[index].project}<br>
                  ${this.pageList[index].sex}<br>
                  ${this.pageList[index].birth}<br>
                  ${this.pageList[index].marriage}<br>
                  ${this.pageList[index].phone}<br>
                  ${this.pageList[index].spouse}<br>
                  ${this.pageList[index].phone2}`
      })
    },
    goEdit (index) {
      this.$router.push({ path: '/Loan/Edit/' + this.pageList[index].id })
    }
  }
}
</script>
<style scoped>
  .right{
    float: right;
    margin: 15px;
  }
</style>
