export default function IdentityCodeValid (code) {
  let city = {
    11: '',
    12: '',
    13: '',
    14: '',
    15: '',
    21: '',
    22: '',
    23: '',
    31: '',
    32: '',
    33: '',
    34: '',
    35: '',
    36: '',
    37: '',
    41: '',
    42: '',
    43: '',
    44: '',
    45: '',
    46: '',
    50: '',
    51: '',
    52: '',
    53: '',
    54: '',
    61: '',
    62: '',
    63: '',
    64: '',
    65: '',
    71: '',
    81: '',
    82: '',
    91: ''
  }
  let tip = ''
  // var pass = true

  if (!code || !/^\d{6}(18|19|20)?\d{2}(0[1-9]|1[012])(0[1-9]|[12]\d|3[01])\d{3}(\d|X)$/i.test(code)) {
    tip = ''
    // pass = false
  } else if (!city[code.substr(0, 2)]) {
    tip = ''
    // pass = false
  } else {
    if (code.length === 18) {
      code = code.split('')
      let factor = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2]
      let parity = [1, 0, 'X', 9, 8, 7, 6, 5, 4, 3, 2]
      let sum = 0
      let ai = 0
      let wi = 0
      for (let i = 0; i < 17; i++) {
        ai = code[i]
        wi = factor[i]
        sum += ai * wi
      }
      if (parity[sum % 11] + '' !== code[17] + '') {
        tip = ''
        // pass = false
      }
    }
  }
  return tip
}
